"use client";

import { useState, useEffect, useCallback } from "react";
import { Vehicle } from "@/store/useStore";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Image from "next/image";
import { cn } from "@/lib/utils";
import { toast } from "sonner";

interface VehicleSwipeCardProps {
  vehicle: Vehicle;
  onSwipe: (vehicleId: string, action: 'like' | 'dislike' | 'superlike') => void;
}

const VehicleSwipeCard = ({ vehicle, onSwipe }: VehicleSwipeCardProps) => {
  const { id, model, price, description, imageUrl, year, mileage } = vehicle;
  const [swipeDirection, setSwipeDirection] = useState<'left' | 'right' | 'up' | null>(null);
  const [isAnimating, setIsAnimating] = useState(false);
  const [imageLoading, setImageLoading] = useState(true);

  const handleImageLoad = useCallback(() => {
    setImageLoading(false);
  }, []);

  const handleSwipeAction = (action: 'like' | 'dislike' | 'superlike') => {
    setIsAnimating(true);
    switch (action) {
      case 'like':
        setSwipeDirection('right');
        toast.success(`Liked ${model}! We'll notify you if it's a match.`, {
          duration: 3000,
        });
        break;
      case 'dislike':
        setSwipeDirection('left');
        toast.error(`Passed on ${model}`, {
          duration: 2000,
        });
        break;
      case 'superlike':
        setSwipeDirection('up');
        toast.success(`Super Liked ${model}! The seller will be notified.`, {
          duration: 3000,
          style: { 
            background: '#8b5cf6',
            color: 'white',
          },
        });
        break;
    }

    // Wait for animation to complete before calling onSwipe
    setTimeout(() => {
      onSwipe(id, action);
      setSwipeDirection(null);
      setIsAnimating(false);
    }, 300);
  };

  useEffect(() => {
    const handleKeyPress = (event: KeyboardEvent) => {
      if (isAnimating) return;
      
      switch (event.key) {
        case 'ArrowLeft':
          handleSwipeAction('dislike');
          break;
        case 'ArrowRight':
          handleSwipeAction('like');
          break;
        case 'ArrowUp':
          handleSwipeAction('superlike');
          break;
      }
    };

    window.addEventListener('keydown', handleKeyPress);
    return () => window.removeEventListener('keydown', handleKeyPress);
  }, [isAnimating]);

  return (
    <div className="relative w-full max-w-md mx-auto">
      <Card 
        className={cn(
          "overflow-hidden shadow-xl transition-all duration-300 transform hover:shadow-2xl",
          isAnimating && swipeDirection === 'left' && "-translate-x-full rotate-[-20deg] opacity-0",
          isAnimating && swipeDirection === 'right' && "translate-x-full rotate-[20deg] opacity-0",
          isAnimating && swipeDirection === 'up' && "-translate-y-full opacity-0"
        )}
      >
        <div className="relative aspect-[4/3] overflow-hidden">
          <div className={cn(
            "absolute inset-0 bg-gray-100 animate-pulse",
            !imageLoading && "hidden"
          )} />
          <Image
            src={imageUrl}
            alt={model}
            fill
            className={cn(
              "object-cover transition-all duration-700",
              imageLoading ? "opacity-0 scale-105" : "opacity-100 hover:scale-105"
            )}
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            priority
            onLoad={handleImageLoad}
          />
          <div className={cn(
            "absolute inset-0 bg-gradient-to-t from-black/60 via-black/25 to-transparent transition-opacity duration-300",
            imageLoading ? "opacity-0" : "opacity-100"
          )} />
          <div className={cn(
            "absolute bottom-4 left-4 right-4 text-white transition-all duration-300",
            imageLoading ? "opacity-0 translate-y-4" : "opacity-100 translate-y-0"
          )}>
            <h2 className="text-3xl font-bold mb-1">{model}</h2>
            <div className="flex justify-between items-center">
              <p className="text-2xl font-semibold">
                ${price.toLocaleString()}
              </p>
              <div className="text-right">
                <p className="text-lg">{year}</p>
                <p className="text-sm opacity-90">{mileage.toLocaleString()} miles</p>
              </div>
            </div>
          </div>
        </div>
        
        <CardContent className="p-6">
          <p className="text-gray-600 text-lg leading-relaxed">{description}</p>
        </CardContent>

        <CardFooter className="relative px-6 py-4 bg-gray-50 flex justify-between gap-4">
          <Button 
            variant="outline" 
            className={cn(
              "flex-1 h-12 text-lg font-medium transition-all duration-200",
              "hover:bg-red-50 hover:text-red-600 hover:border-red-200",
              swipeDirection === 'left' && "bg-red-50 text-red-600 border-red-200"
            )}
            onClick={() => handleSwipeAction('dislike')}
            disabled={isAnimating}
          >
            Pass
          </Button>
          <Button 
            variant="outline" 
            className={cn(
              "flex-1 h-12 text-lg font-medium transition-all duration-200",
              "hover:bg-purple-50 hover:text-purple-600 hover:border-purple-200",
              swipeDirection === 'up' && "bg-purple-50 text-purple-600 border-purple-200"
            )}
            onClick={() => handleSwipeAction('superlike')}
            disabled={isAnimating}
          >
            Super Like
          </Button>
          <Button 
            className={cn(
              "flex-1 h-12 text-lg font-medium transition-all duration-200",
              "hover:bg-green-600",
              swipeDirection === 'right' && "bg-green-600"
            )}
            onClick={() => handleSwipeAction('like')}
            disabled={isAnimating}
          >
            Like
          </Button>
        </CardFooter>
      </Card>
      
      <div className="absolute -bottom-8 left-1/2 transform -translate-x-1/2 text-sm bg-white/90 backdrop-blur-sm px-4 py-2 rounded-full shadow-md border border-gray-100/50 transition-opacity duration-300 hover:bg-white">
        <div className="flex items-center gap-2 text-gray-600">
          <span className="flex items-center gap-1">
            <kbd className="px-2 py-1 bg-gray-100 rounded text-xs">←</kbd>
            <span>Pass</span>
          </span>
          <span className="text-gray-300">|</span>
          <span className="flex items-center gap-1">
            <kbd className="px-2 py-1 bg-gray-100 rounded text-xs">→</kbd>
            <span>Like</span>
          </span>
          <span className="text-gray-300">|</span>
          <span className="flex items-center gap-1">
            <kbd className="px-2 py-1 bg-gray-100 rounded text-xs">↑</kbd>
            <span>Super Like</span>
          </span>
        </div>
      </div>
    </div>
  );
};

export default VehicleSwipeCard;
