"use client";

import { useEffect } from "react";
import useStore from "@/store/useStore";
import VehicleSwipeCard from "@/components/VehicleSwipeCard";
import { Card } from "@/components/ui/card";

// Mock data for initial development
const MOCK_VEHICLES = [
  {
    id: "1",
    model: "Tesla Model 3",
    price: 35000,
    description: "All-electric vehicle with autopilot capability. Great condition, low mileage.",
    imageUrl: "https://images.pexels.com/photos/7674867/pexels-photo-7674867.jpeg",
    year: 2023,
    mileage: 15000
  },
  {
    id: "2",
    model: "BMW M3",
    price: 45000,
    description: "High-performance luxury sedan. Well maintained with full service history.",
    imageUrl: "https://images.pexels.com/photos/892522/pexels-photo-892522.jpeg",
    year: 2022,
    mileage: 25000
  },
  {
    id: "3",
    model: "Mercedes-Benz C300",
    price: 42000,
    description: "Elegant and powerful. Premium features with advanced safety systems.",
    imageUrl: "https://images.pexels.com/photos/4674337/pexels-photo-4674337.jpeg",
    year: 2023,
    mileage: 18000
  }
];

export default function Home() {
  const { 
    vehicles,
    currentVehicleIndex,
    loading,
    error,
    setVehicles,
    setCurrentVehicleIndex,
    addMatch
  } = useStore();

  useEffect(() => {
    // In a real app, this would be an API call
    setVehicles(MOCK_VEHICLES);
  }, [setVehicles]);

  const handleSwipe = async (vehicleId: string, action: 'like' | 'dislike' | 'superlike') => {
    // In a real app, this would be an API call
    if (action === 'like' || action === 'superlike') {
      addMatch({
        id: Math.random().toString(),
        vehicleId,
        userId: "user-1", // In a real app, this would come from auth
        timestamp: new Date()
      });
    }

    // Move to next vehicle
    setCurrentVehicleIndex(currentVehicleIndex + 1);
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <Card className="p-6">Loading...</Card>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <Card className="p-6 text-red-500">{error}</Card>
      </div>
    );
  }

  if (!vehicles.length) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <Card className="p-6">No vehicles available</Card>
      </div>
    );
  }

  if (currentVehicleIndex >= vehicles.length) {
    return (
      <div className="flex justify-center items-center min-h-screen">
        <Card className="p-6">
          <h2 className="text-xl font-bold mb-2">No More Vehicles</h2>
          <p className="text-gray-600">Check back later for new listings!</p>
        </Card>
      </div>
    );
  }

  return (
    <main className="min-h-screen p-4 md:p-8 flex flex-col items-center justify-center bg-gray-50">
      <div className="w-full max-w-md mx-auto">
        <h1 className="text-3xl font-bold text-center mb-8">Tindrive</h1>
        <VehicleSwipeCard
          vehicle={vehicles[currentVehicleIndex]}
          onSwipe={handleSwipe}
        />
      </div>
    </main>
  );
}
